import React from "react";
import { DeleteUser } from "../../service/UserService";
import { toast } from 'react-toastify';

const ModalDelete  = (props) => {
    const {showModalDelete , handleShowModalDelete, dataDelete,TableWhenDelete} = props
    const ConfirmDelete = async () => {
        let res = await DeleteUser (dataDelete.id)
        if(res){
            toast.success("Xa thnh cng!")
            handleShowModalDelete(true)
            TableWhenDelete(dataDelete)
        }
    }
    return (
        <>
        {
            showModalDelete === false &&
            <div className="modal-delete">
                <div className="delete-form">
                    <h3>Xa hc sinh</h3>
                    <p>Bn chc mun xa thng tin ca hc sinh c </p>
                    <p>H v Tn l "<b>{dataDelete.lastName}</b> <b>{dataDelete.firstName}</b> " ny khng !</p>
                    <br/>
                    

                    <div>
                        <button onClick={() => ConfirmDelete()}>Xa</button>
                        <button onClick={() => handleShowModalDelete(true)}>Thot</button>
                    </div>
                </div>
            </div>
        }
        </>
    )
}

export default ModalDelete